import QueryString from "qs";
import React, { Fragment, useEffect, useState } from "react";
import { getProductsPage } from "../../../api/products";
import { addToCart } from "../../../actions/cart";
import ProductCard from "../../../components/features/product";
import ChooseVariationModal from "../../../components/features/modal/choose-variation";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

const AllProducts = ({ menu }) => {
	let mount = true;
	const [data, setData] = useState({ data: [] });
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } }
	});
	const { t } = useTranslation();

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}

	useEffect(() => {
		getProductsPage(
			menu.id,
			QueryString.stringify({ page: 1, size: 10 })
		).then(res => {
			if (mount) setData({ data: res.variations });
		});

		return () => {
			mount = false;
		};
	}, []);

	return (
		<Fragment>
			<div className="blog-title">{t("all-products")}</div>
			<div className="all-products row">
				{data?.data.map((product, index) => {
					return (
						<Fragment key={index}>
							<ProductCard
								product={product}
								type="sidebar"
								onAddToCart={dataProduct => {
									onAddToCart(dataProduct);
								}}
							/>
						</Fragment>
					);
				})}
			</div>
			<Link to={`category/${menu.slug}`} className="show-all-btn mb-5">
				{t("show-all")}
			</Link>
			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</Fragment>
	);
};

export default AllProducts;
