import React, { useMemo } from "react";
import { Helmet } from "react-helmet";
import QuickView from "../../../components/features/product/QuickViewNew";
import Slider from "../components/banners/Slider";
import { useSelector } from "react-redux";
import CollectionsWithTab from "../components/collections/CollectionsWithTab";
import ImageVideoCard from "../components/ImageVideoCard";
import CategoryCard from "../components/blocks/CategoryCard";
import AllProducts from "../components/AllProducts";
import HorizontalMenuThree from "../../../components/common/Menus/Desktop/HorizontalMenuThree";
import { getImageUrl } from "../../../utils/oxMethods";
import FullBlock from "../components/blocks/FullBlock";

function HomePageThree() {
	const { menuVertical } = useSelector((state) => state.menu);
	const marketData = useSelector((state) => state.marketData);
	const { blocks } = useSelector((state) => state.blocks);

	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_"),
		};
	}, []);

	if (!menuVertical.length) {
		return "";
	}
	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="16x16"
				/>
			</Helmet>
			<h1 className="d-none">{head.title}</h1>
			<div className="main home-page-three">
				<div className="container">
					<HorizontalMenuThree />

					<Slider />

					<ImageVideoCard />

					<div className="pt-2 pb-2">
						<div className="featured">
							<CollectionsWithTab blocks={blocks} />
						</div>
					</div>

					<div className="blog-content">
						{!blocks ? (
							<div>Loading...</div>
						) : (
							blocks
								.filter((block) => block.title === "big_block")
								.map((block, key) => (
									<FullBlock key={key} block={block} />
								))
						)}
					</div>

					<div className="category-box">
						{blocks ? (
							<CategoryCard
								block={blocks.find(
									(b) => b.title === "six_block"
								)}
							/>
						) : null}
					</div>

					<AllProducts
						menu={menuVertical.sort((a, b) => a.sort - b.sort)[0]}
					/>
					<QuickView />
				</div>
			</div>
		</>
	);
}

export default HomePageThree;
