import { Splide, SplideSlide } from "@splidejs/react-splide";
import React from "react";
import { brandSlider } from "../../../../components/options";
import Banner from "../banners/Banner";

function Brand(props) {
	const { adClass = "mt-1 owl-simple", brands } = props;

	return (
		<Splide
			adClass={`${adClass} brand-carousel cols-xl-7 cols-lg-5 cols-md-4 cols-sm-3 cols-2`}
			options={brandSlider}
			carouselId="brandslider"
		>
			{brands.map((item, index) => {
				return (
					<SplideSlide key={index}>
						<Banner item={item} />
					</SplideSlide>
				);
			})}
		</Splide>
	);
}

export default Brand;
