import React, { useState, useMemo } from "react";
import Brand from "../brands/Brand";
import Slider from "../banners/Slider";
import { addToCart } from "../../../../actions/cart";
import ProductCard from "../../../../components/features/product";
import ChooseVariationModal from "../../../../components/features/modal/choose-variation";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";
// import Submenu from "../submenu";
import { dt } from "../../../../i18n";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import {
	featuredSlider2,
	featuredSlider3,
} from "../../../../components/options";
import { useTranslation } from "react-i18next";

const FullBlock = ({ block }) => {
	const { t } = useTranslation();
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});

	const banner = useMemo(() => {
		return block.dynamicCollectionBlock.find(
			(block) => block.name === "banner"
		);
	}, [block]);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	return (
		block.title === "big_block" && (
			<div className="block-section mt-10">
				<h3 className="blog-title">{dt(block.name)}</h3>

				{block.dynamicCollectionBlock.map((item, i) => {
					return item.name === "brands" ? (
						<div className="brand-box" key={i}>
							<Brand brands={item.photos} />
						</div>
					) : item.name === "slide" ? (
						<Slider key={i} slider={item.photos} size="800x_" />
					) : null;
				})}

				{/* <div className="submenu-box">
					<Submenu submenu={block.menus} />
				</div> */}

				<div className="simple-carousel">
					<div className="row">
						<div className="col-md-12">
							{block.products ? (
								<Splide
									adclass="owl-full carousel-equal-height"
									options={featuredSlider2}
								>
									{block.products
										.slice(4)
										.map((product, index) => {
											return (
												<SplideSlide key={index}>
													<ProductCard
														product={product}
														type="sidebar"
														onAddToCart={(
															dataProduct
														) => {
															onAddToCart(
																dataProduct
															);
														}}
													/>
												</SplideSlide>
											);
										})}
								</Splide>
							) : (
								<ShimmerProducts columns={10} />
							)}

							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</div>
					</div>
				</div>

				<div className="carousel-with-banner">
					<div className="row">
						<div className="col-md-10">
							{block.products ? (
								<Splide
									adclass="owl-full carousel-equal-height carousel-with-shadow"
									options={featuredSlider3}
								>
									{block.products
										.slice(0, 4)
										.map((product, index) => {
											return (
												<SplideSlide key={index}>
													<ProductCard
														product={product}
														type="sidebar"
														onAddToCart={(
															dataProduct
														) => {
															onAddToCart(
																dataProduct
															);
														}}
													/>
												</SplideSlide>
											);
										})}
								</Splide>
							) : (
								<ShimmerProducts
									columns={10}
									className={"col-3"}
								/>
							)}

							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</div>
						<div className="col-md-2">
							<div className="right-banner">
								{banner ? (
									<a
										href={banner.photos[0].link}
										target="blank"
									>
										<img
											src={
												banner.photos[0].urls[
													"original"
												]
											}
											alt={block.name}
										/>
									</a>
								) : null}
							</div>
						</div>
					</div>
				</div>
				{block.footerTitle && (
					<a
						href={block.footerTitle}
						className="show-all-btn mb-5 blog-show-all-btn"
					>
						{t("show-all")}
					</a>
				)}
			</div>
		)
	);
};

export default FullBlock;
