import React from "react";
import { useSelector } from "react-redux";

const ImageVideoCard = () => {
	const banners = useSelector((state) => state.any.banners);
	let banner = banners.find((b) => b.position === "top");

	return (
		<div className="row mb-3">
			<div className="col-md-6">
				<div className="img-box">
					{banner ? (
						<a href={banner.link}>
							<img
								src={banner.image.urls.original}
								alt="banner"
							/>
						</a>
					) : null}
				</div>
			</div>
			<div className="col-md-6">
				<div className="video-box">
					{/* <iframe
						src="https://www.youtube.com/embed/yFPmgIzYj1k"
						title="YouTube video player"
						frameBorder="0"
						allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						allowFullScreen
					></iframe> */}
					<iframe
						src="https://www.youtube.com/embed/0fjdjfUWUkk"
						title="YouTube video player"
						frameBorder="0"
						allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						allowFullScreen
					></iframe>
				</div>
			</div>
		</div>
	);
};

export default ImageVideoCard;
